import React, { FC, useEffect, useState } from "react";

import { chartJS } from "utils/ChartJS";

import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import {
  getBackgroundColorsForArray,
  getBackgroundColorForDot,
} from "./dotMethods";
import { setChartSettings } from "./chartSettings";

const colors = {
  dots: {
    red: "#DD5389",
    green: "#109df8",
  },

  background: {
    red: "#DD5389",
    green: "#109df8",
  },
};

const Graph: FC = () => {
  const [canvas, setCanvas] = useState<HTMLCanvasElement | null>(null);
  const [chart, setChart] = useState<Chart[]>([]);

  useEffect(() => {
    const removeListener = liveStatsManager.getDots((history) => {
      if (chart[0] && canvas) {
        const { data } = chart[0];
        if (history.length <= 1) {
          chart[0].data.labels = [...history];
          data?.datasets?.forEach((dataset, i) => {
            dataset.data = [...history];
          });
          chart[0].update();
        } else {
          const newDot = history[history.length - 1];
          if (data?.datasets && data?.labels) {
            const { datasets, labels } = data;
            labels.push(newDot);
            datasets.forEach((dataset, i) => {
              if (dataset.data) {
                dataset.data.push(newDot);
                if (Array.isArray(dataset.pointHoverBackgroundColor)) {
                  dataset.pointHoverBackgroundColor =
                    getBackgroundColorsForArray(
                      //@ts-ignore
                      dataset.data,
                      {
                        red: colors.dots.red,
                        green: colors.dots.green,
                      },
                    );
                } else {
                  dataset.pointHoverBackgroundColor =
                    getBackgroundColorsForArray(
                      //@ts-ignore
                      dataset.data,
                      {
                        red: colors.dots.red,
                        green: colors.dots.green,
                      },
                    );
                }

                dataset.borderColor = getBackgroundColorForDot(newDot, {
                  red: colors.background.red,
                  green: colors.background.green,
                });
                dataset.backgroundColor = "transparent";
              } else {
                dataset.data = [];
              }
            });
          }

          chart[0].update();
        }
      } else if (canvas) {
        chartJS.get((Chart) => {
          setChart((old) => {
            old.forEach((chart) => {
              chart?.destroy();
            });
            return [new Chart(canvas, setChartSettings(history, colors))];
          });
        });
      }
    });

    return () => {
      removeListener();
      chart?.forEach((graph) => graph?.destroy());
    };
  }, [canvas, chart]);

  return (
    <div>
      <canvas
        width="275"
        height="200"
        ref={(node) => {
          setCanvas(node);
        }}
      />
    </div>
  );
};

export default React.memo(Graph);
