import React, { useCallback, useEffect, useState } from "react";
import SwipeableViews from "react-swipeable-views";

import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";

import Rase from "./Rase";
import Bets from "./Bets";
import Tabs from "./Tabs";

import css from "./style.module.scss";

const Body = () => {
  const [isRaseShow, setIsRaseShow] = useState(false);

  const tabChange = useCallback((value: boolean) => {
    setIsRaseShow(value);
  }, []);

  useEffect(() => {
    liveStatsManager.open();
  }, []);

  return (
    <div className={css.root}>
      <Tabs value={isRaseShow} tabChange={tabChange} />

      <SwipeableViews
        disabled
        disableLazyLoading={false}
        index={isRaseShow ? 1 : 0}>
        <Bets />
        <Rase show={isRaseShow} />
      </SwipeableViews>
    </div>
  );
};

export default Body;
