import React, { FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";

interface ScoreData {
  reward: string;
  total: string;
}

interface Props {
  data: ScoreData;
}

const Score: FC<Props> = ({ data }) => {
  const intl = useGetLocale();

  const { reward, total } = data;

  return (
    <div className={css.root}>
      <div className={css.dataWrapper}>
        <h4 className={css.title}>
          {intl.formatMessage({
            id: "liveStats.rase.reward",
          })}
          :
        </h4>
        <p
          className={clsx(css.text, {
            [css.textLose]: Number(reward) < 0,
            [css.textWin]: Number(reward) >= 0,
          })}>
          {reward} USD
        </p>
      </div>

      <div className={css.dataWrapper}>
        <h4 className={css.title}>
          {intl.formatMessage({
            id: "liveStats.rase.total",
          })}
          :
        </h4>
        <p
          className={clsx(css.text, {
            [css.textLose]: Number(reward) < 0,
            [css.textWin]: Number(reward) >= 0,
          })}>
          {total} USD
        </p>
      </div>
    </div>
  );
};

export default Score;
