import React, { useEffect, useState, FC } from "react";
import { useGetLocale } from "providers";

import { useFetch, useInterval } from "hooks";
import {
  UserWagered,
  WageringType,
} from "components/Statistics/Tabs/WageringRace/types";
import { useUser } from "providers/User";

import Score from "./Score";
import Time from "./Time";
import css from "./style.module.scss";

const timeouts: number[] = [];

const clearTimeouts = () => {
  timeouts.forEach((timeout) => clearInterval(timeout));
  timeouts.length = 0;
};

interface Props {
  show: boolean;
}

const Rase: FC<Props> = ({ show }) => {
  const intl = useGetLocale();
  const { user } = useUser();

  const [contestFetch, { data: contestData }] =
    useFetch<WageringType>("statistics/contest");
  const [personalFetch, { data: personalData }] = useFetch<UserWagered>(
    "statistics/contest-personal",
  );
  const [myData, setMyData] = useState({
    reward: "0",
    total: (0).toFixed(8),
  });
  const [timeEndOn, setTimeEndOn] = useState<number | null>(null);

  useInterval(personalFetch, 20000);

  useEffect(() => {
    personalFetch();
    contestFetch();

    const interval = setInterval(contestFetch, 20000);
    timeouts.push(interval);

    return () => {
      clearTimeouts();
    };
  }, []);

  useEffect(() => {
    if (personalData) {
      const data = {
        ...myData,
        total: personalData.my_wagered_amount,
      };

      if (JSON.stringify(data) !== JSON.stringify(myData)) {
        setMyData(data);
      }
    }
  }, [personalData]);

  useEffect(() => {
    if (contestData) {
      const time = contestData.wagering_race_ends_on.padEnd(13, "0");

      if (Number(time) - Date.now() >= 0) {
        setTimeEndOn(Number(time));
      } else {
        setTimeEndOn(null);
      }

      contestData.wagering_race_leaderboard.forEach((bet) => {
        if (bet.user_name === user?.user_name) {
          setMyData({
            ...myData,
            reward: bet.reward_usd,
          });
        }
      });
    }
  }, [contestData]);

  if (!show) {
    return null;
  }

  return (
    <div className={css.root}>
      <h2 className={css.title}>
        $350 USD - 24{" "}
        {intl.formatMessage({
          id: "liveStats.rase.day",
        })}
      </h2>
      <div className={css.content}>
        <Time time={timeEndOn} show={show} />
        <Score data={myData} />
      </div>
    </div>
  );
};

export default Rase;
