import React, { FC, useCallback, useEffect, useRef, useState } from "react";

import { useInterval } from "hooks";

import { ReactComponent as Circle } from "./circle.svg";
import css from "./style.module.scss";
interface Props {
  time: number;
}

const getTimePercent = (finalTime: number) => {
  const getDifference = (numberOfDays: number) =>
    new Date(finalTime - 24 * numberOfDays * 3600 * 1000).getTime();

  let days = 1;
  let difference = getDifference(days);
  while (difference > Date.now()) {
    difference = getDifference(++days);
    if (days > 1000) {
      difference = Date.now();
      break;
    }
  }

  return ((Date.now() - difference) * 100) / Math.abs(finalTime - difference);
};

const TimeLoader: FC<Props> = ({ time }) => {
  const [percent, setPercent] = useState(-1);
  const svgRef = useRef<SVGSVGElement>(null);

  const calcPercent = useCallback(() => {
    setPercent(getTimePercent(time));
  }, [time]);

  useInterval(calcPercent, 60000 * 5);

  useEffect(() => {
    calcPercent();
  }, [time]);

  useEffect(() => {
    if (svgRef.current) {
      //@ts-ignore
      const circle: SVGCircleElement = svgRef.current.lastChild;

      circle.style.strokeDashoffset =
        percent > 0 ? (314 * (percent / 100)).toFixed(2) : "314";
    }
  }, [svgRef.current, percent]);

  return (
    <>
      <Circle className={css.timeCircle} ref={svgRef} />
    </>
  );
};

export default React.memo(TimeLoader);
