import { replaceNumbers } from "utils";
import type ChartJS from "chart.js";

import { getBackgroundColorForDot } from "./dotMethods";

interface Colors {
  dots: {
    red: string;
    green: string;
  };
  background: {
    red: string;
    green: string;
  };
}

const tooltipsCallbacks: Chart.ChartTooltipCallback = {
  label: (item, data) => {
    return replaceNumbers(Number(item.value).toFixed(8));
  },
};

export const setChartSettings = (
  history: number[] | string[],
  colors: Colors,
): Chart.ChartConfiguration => {
  return {
    type: "line",
    data: {
      labels: [...history],
      datasets: [
        {
          //@ts-ignore
          data: [...history],
          backgroundColor: "transparent",
          // backgroundColor: getBackgroundColorForDot(
          //   history[history.length - 1],
          //   colors.background,
          // ),
          borderColor: getBackgroundColorForDot(
            history[history.length - 1],
            colors.background,
          ),
          pointBackgroundColor: "rgba(255, 255, 255, 0)",
          pointBorderColor: "rgba(255, 255, 255, 0)",
          pointHoverBackgroundColor: colors.dots.green,
          pointRadius: 10,
          pointHoverRadius: 5,
          borderWidth: 2,

          label: "",
        },
      ],
    },
    options: {
      elements: {
        line: {
          // tension: 0,
        },
        point: {
          radius: 0,
        },
      },
      layout: {
        padding: 10,
      },
      hover: {
        mode: "index",
      },
      responsive: true,
      animation: {
        duration: 1500,
        easing: "easeInOutCubic",
      },
      legend: {
        display: false,
      },
      tooltips: {
        caretSize: 0,
        titleFontColor: "transparent",
        titleFontSize: 0,
        displayColors: false,
        callbacks: tooltipsCallbacks,
      },

      scales: {
        xAxes: [
          {
            display: false,
            ticks: {
              beginAtZero: true,
            },
          },
        ],
        yAxes: [
          {
            display: "2",
            ticks: {
              beginAtZero: false,
              callback: (value, i, src) => {
                if (Number(value) === 0 || i === 0 || i === src.length - 1) {
                  return replaceNumbers(Number(value).toFixed(8));
                } else {
                  return "";
                }
              },
            },
          },
        ],
      },
    },
  };
};
