import React, { FC, CSSProperties, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useCoins } from "hooks";
import { Button } from "components";
import { GetCoinImg } from "components/CoinImg";
import { ReactComponent as Refresh } from "assets/icons/autorenew.svg";

import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import { LiveStatsDataResult } from "components/LiveStats/eventEmitter/types";

import Graph from "./Graph";
import css from "./style.module.scss";

const coinStyle: CSSProperties = {
  left: -5,
};

const liveClear = () => liveStatsManager.restart();

const Bets: FC = () => {
  const intl = useGetLocale();
  const { showInUSD, coinsPrices } = useCoins();

  const [data, setData] = useState<LiveStatsDataResult>({
    generalLiveData: {
      amount: 0,
      profit: 0,
      coin: "GEM",
    },
    statistics: {
      loses: 0,
      wins: 0,
    },
  });

  useEffect(() => {
    const removeListener = liveStatsManager.getData((value) => {
      setData(value);
    });

    return () => {
      removeListener();
    };
  }, []);

  const { generalLiveData, statistics } = data;

  return (
    <div className={css.root}>
      <Graph />

      <div className={css.textBox}>
        <span className={css.text}>
          <h5 className={css.textTitle}>
            {intl.formatMessage({
              id: "liveStats.bets.profit",
            })}
          </h5>
          <span className={css.profitWrapper}>
            <GetCoinImg coin={generalLiveData.coin} style={coinStyle} />
            <p
              className={clsx(css.textContent, {
                [css.win]: generalLiveData.profit >= 0,
                [css.lose]: generalLiveData.profit < 0,
              })}>
              {showInUSD
                ? `$${(
                    generalLiveData.profit *
                    Number(coinsPrices[generalLiveData.coin].price)
                  ).toFixed(8)}`
                : generalLiveData.profit.toFixed(8)}
            </p>
          </span>
        </span>

        <div />

        <span className={css.text}>
          <h5 className={css.textTitle}>
            {intl.formatMessage({
              id: "liveStats.bets.amount",
            })}
          </h5>

          <span className={css.profitWrapper}>
            <GetCoinImg coin={generalLiveData.coin} style={coinStyle} />
            <p className={css.textContent}>
              {" "}
              {showInUSD
                ? `$${(
                    generalLiveData.amount *
                    Number(coinsPrices[generalLiveData.coin].price)
                  ).toFixed(8)}`
                : generalLiveData.amount.toFixed(8)}
            </p>
          </span>
        </span>

        <span className={css.text}>
          <h5 className={css.textTitle}>
            {intl.formatMessage({
              id: "liveStats.bets.wins",
            })}
          </h5>
          <p className={clsx(css.textContent, css.win)}> {statistics.wins}</p>
        </span>

        <div />

        <span className={css.text}>
          <h5 className={css.textTitle}>
            {intl.formatMessage({
              id: "liveStats.bets.loses",
            })}
          </h5>
          <p className={clsx(css.textContent, css.lose)}> {statistics.loses}</p>
        </span>
      </div>

      <div className={css.buttonWrapper}>
        <Button
          className={css.refreshButton}
          onClick={liveClear}
          classes={{ label: css.buttonLabel }}>
          <Refresh className={css.refreshIcon} />
        </Button>
      </div>
    </div>
  );
};

export default Bets;
