interface Colors {
  red: string;
  green: string;
}

export const getBackgroundColorsForArray = (dots: number[], colors: Colors) => {
  return dots.map((value) => {
    if (Number(value) < 0) {
      return colors.red;
    } else {
      return colors.green;
    }
  });
};

export const getBackgroundColorForDot = (
  dot: number | string,
  colors: Colors,
) => {
  if (Number(dot) < 0) {
    return colors.red;
  } else {
    return colors.green;
  }
};

export const fixLength = (array: any[], length: number) => {
  while (array.length > length) {
    array.shift();
  }
};
