import React, { FC } from "react";
import Countdown from "react-countdown";
import { useGetLocale } from "providers";

import TimeLoader from "./Loader";
import css from "./style.module.scss";

interface Props {
  time: number | null;
  show: boolean;
}

const Time: FC<Props> = ({ time, show }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <TimeLoader time={show ? time || Date.now() : Date.now()} />
      <div className={css.timeWrapper}>
        {time ? (
          <Countdown overtime date={time} className={css.timeText} />
        ) : (
          <p className={css.timeText}>
            {intl.formatMessage({
              id: "gameTemplate.statistics.wageringTable.processing",
            })}
          </p>
        )}
      </div>
    </div>
  );
};

export default Time;
