import React, { useCallback, FC } from "react";
import { useGetLocale } from "providers";

import { Tab, Tabs as TabsWrapper } from "components";

import css from "./style.module.scss";
interface Props {
  value: boolean;
  tabChange: (newValue: boolean) => void;
}

const Tabs: FC<Props> = ({ value, tabChange }) => {
  const intl = useGetLocale();

  const handleChange = useCallback((ev: any, value: number | string) => {
    tabChange(value === 1);
  }, []);

  return (
    <div className={css.root}>
      <TabsWrapper
        highlighterClass={css.highlighter}
        className={css.wrapper}
        value={value ? 1 : 0}
        onChange={handleChange}>
        <Tab
          className={css.tab}
          classes={{
            label: css.tabText,
          }}>
          {intl.formatMessage({
            id: "liveStats.bets.title",
          })}
        </Tab>
        <Tab
          className={css.tab}
          classes={{
            label: css.tabText,
          }}>
          {intl.formatMessage({
            id: "liveStats.rase.title",
          })}
        </Tab>
      </TabsWrapper>
    </div>
  );
};

export default Tabs;
